import React, { useRef } from "react"
import { TrafficLightReg } from "../../../jacdac-ts/src/jdom/constants"
import { DashboardServiceProps } from "./DashboardServiceWidget"
import { useRegisterBoolValue } from "../../jacdac/useRegisterValue"
import SvgWidget from "../widgets/SvgWidget"
import useWidgetTheme from "../widgets/useWidgetTheme"
import useServiceServer from "../hooks/useServiceServer"
import useWidgetSize from "../widgets/useWidgetSize"
import useSvgButtonProps from "../hooks/useSvgButtonProps"
import TrafficLightServer from "../../../jacdac-ts/src/servers/trafficlightserver"
import useKeyboardNavigationProps from "../hooks/useKeyboardNavigationProps"
import useRegister from "../hooks/useRegister"

const m = 2
const r = 8
const ri = 7
const w = 2 * r + 2 * m
const h = 4 * w + 2 * m
const cx = w / 2
const sw = 2

function TrafficLight(props: {
    cx: number
    cy: number
    label: string
    background: string
    fill: string
    onDown?: () => void
}) {
    const { cx, cy, fill, background, label, onDown } = props
    const buttonProps = useSvgButtonProps<SVGCircleElement>(label, onDown)

    return (
        <>
            <circle cx={cx} cy={cy} r={r} fill={background} stroke={"none"} />
            <circle
                cx={cx}
                cy={cy}
                r={ri}
                fill={fill}
                strokeWidth={sw}
                {...buttonProps}
            />
        </>
    )
}

export default function DashboardTrafficLight(props: DashboardServiceProps) {
    const { service, services, variant } = props

    const widgetRef = useRef<SVGGElement>()

    const redRegister = useRegister(service, TrafficLightReg.Red)
    const orangeRegister = useRegister(service, TrafficLightReg.Orange)
    const greenRegister = useRegister(service, TrafficLightReg.Green)

    const red = useRegisterBoolValue(redRegister, props)
    const orange = useRegisterBoolValue(orangeRegister, props)
    const green = useRegisterBoolValue(greenRegister, props)

    const lightRegs = [
        TrafficLightReg.Red,
        TrafficLightReg.Orange,
        TrafficLightReg.Green,
    ]
    const lights = [red, orange, green]

    const server = useServiceServer<TrafficLightServer>(service)
    const color = server ? "secondary" : "primary"
    const { background, controlBackground } = useWidgetTheme(color)
    const widgetSize = useWidgetSize(variant, services?.length)

    let cy = 0
    const names = ["red", "yellow", "green"]
    const colors = ["red", "orange", "green"]
    const navProps = useKeyboardNavigationProps(widgetRef.current)
    return (
        <SvgWidget width={w} height={h} size={widgetSize}>
            <g ref={widgetRef} {...navProps}>
                <rect
                    x={0}
                    y={0}
                    width={w}
                    height={h}
                    rx={m}
                    fill={background}
                />
                {lights.map((v, i) => {
                    cy += m + 2 * r
                    const fill = v ? colors[i] : controlBackground
                    const onDown = async () => {
                        const reg = service.register(lightRegs[i])
                        await reg.sendSetBoolAsync(!v, true)
                    }
                    return (
                        <TrafficLight
                            key={i}
                            cx={cx}
                            cy={cy}
                            background={background}
                            fill={fill}
                            onDown={onDown}
                            label={`${names[i]} ${v ? "on" : "off"}`}
                        />
                    )
                })}
            </g>
        </SvgWidget>
    )
}
